.brazzers-daddy:after {
	content: "";
	display: table;
	clear: both
}

.brazzers-daddy {
	position: relative
}

.brazzers-daddy img {
	width: 100%;
	position: relative;
	display: none;
	top: 0;
	left: 0;
}

.brazzers-daddy img:first-child {
	display: block
}

.tmb-wrap {
	position: absolute;
	z-index: 2;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%
}

.tmb-wrap .tmb-wrap-table {
	display: table;
	height: 100%;
	width: 100%
}

.tmb-wrap-table div {
	display: table-cell;
	padding-top: 10px;
	transition: all .25s ease
}

.tmb-wrap-table div:first-child:last-child {
	border-color: transparent
}